<template>
    <div>
        player{{parentMsg}}
        <input type="button" value="" @click ="sendToParent">
    </div>
</template>
<script>
export default {
  name: "player",
  props: ["parentMsg"],
  data: function() {
    return {
      childMsg: "1"
    };
  },
  methods: {
    sendToParent: function() {
      Event.$emit("child-event", this.childMsg);
    }
  }
};
</script>



