<template>
  <div id="app">
    <p>{{count}}</p>
    <p>{{oddCount}}</p>
    <div class="main">
      <div class="inner"></div>
    </div>
    <input type="button" @click="increment" value="">
    <input type="button" @click="clickOdd" value="">
    <input type="button" @click="clickAsync" value="">
  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'

import Player from '../components/player.vue'
export default {
  name: 'app',
  computed:mapGetters(['count','oddCount']),
  methods:mapActions(['increment','clickOdd','clickAsync'])
}
</script>

<style lang="less">
@import url(./assets/css/main.less);
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}


</style>



