import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
    count: 10
}

const getters = {
    count(state) {
        return state.count
    },
    oddCount(state){
        return state.count%2 ? '':'' 
    }
}

const mutations = {
    increment(state) {
        state.count++
    },
    clickOdd(state) {
        state.count++
    },
    clickAsync(state) {
        state.count++
    }
}
const actions = {
    increment: ({ commit }) => {
        commit('increment')
    },
    clickOdd: ({ commit, state }) => {  //state
        if (state.count % 2 === 0) {
            commit('clickOdd')
        }
    },
    clickAsync: ({ commit }) => {
        new Promise((resolve) => {
            setTimeout(() => {
                commit('clickAsync')
            }, 1000);
        })
    }
}

export default new Vuex.Store({
    state,
    mutations,
    actions,
    getters
})


